/* Ïù¥ÎØ∏ÏßÄ Í∞êÏã∏Îäî ÏòÅÏó≠ */
.image-wrapper {
position: relative;
display: inline-block;
}

/* ÏÉÅÌíà ÏÉÅÏÑ∏ Ïù¥ÎØ∏ÏßÄ */
.image-wrapper img {
width: 100%;
max-width: 450px;
border-radius: 15px;
object-fit: cover;
}

.back-button {
position: absolute;
top: -18px;      /* ‚≠ê ÏúÑÎ°ú */
right: -200px;    /* ‚≠ê Ïò§Î•∏Ï™ΩÏúºÎ°ú */

width: 38px;
height: 38px;
border-radius: 50%;

border: none;
background: rgba(0, 0, 0, 0.55);
backdrop-filter: blur(6px);

color: white;
font-size: 16px;
cursor: pointer;

display: flex;
align-items: center;
justify-content: center;

z-index: 20;
}


.image-wrapper:hover .back-button {
background: rgba(0, 0, 0, 0.6);
transform: scale(1.08);
}
.price-box {
display: flex;
justify-content: center;
align-items: center;
gap: 8px;
margin-top: 6px;
}

.original-price {
font-size: 15px;
color: #999;
text-decoration: line-through;
}

.sale-price {
font-size: 15px;
font-weight: bold;
color: #ee294a;
}

.sale-percent {
font-size: 15px;
color: #ff4d4d;
font-weight: bold;
}

.products-grid {
margin-top: 40px;
display: grid;                    /* ‚≠ê Ï§ëÏöî */
grid-template-columns: repeat(4, 1fr);  /* ‚≠ê 4Í∞úÏî© */
gap: 30px;
max-width: 1200px;
margin: 40px auto;
}

.product-card h3 {
font-size: 13px;        /* üîΩ Í∏ÄÏî® ÌÅ¨Í∏∞ */
line-height: 1.4;      /* üîΩ Ï§Ñ Í∞ÑÍ≤© */
margin: 10px 0 6px;    /* ÏúÑ / ÏïÑÎûò Ïó¨Î∞± */
display: -webkit-box;
-webkit-line-clamp: 2; /* ‚≠ê 2Ï§Ñ Ï†úÌïú */
-webkit-box-orient: vertical;
overflow: hidden;
}

/* ===== ÏÉÅÌíà ÏÉÅÏÑ∏ ===== */
.image-wrapper {
position: relative;
max-width: 450px;
margin: 0 auto;
}

.image-wrapper img {
width: 100%;
border-radius: 16px;
}

.detail-title {
text-align: center;
margin-top: 20px;
font-size: 20px;
font-weight: 700;
}

.detail-price {
display: flex;
justify-content: center;
gap: 10px;
margin: 15px 0;
align-items: center;
}

.detail-desc {
white-space: pre-line;   /* Ï§ÑÎ∞îÍøà Ïú†ÏßÄ */
font-size: 15px;       /* üîΩ Ïó¨Í∏∞ÏÑú Ï°∞Ï†à */
line-height: 1.9;
color:#18aa92;
max-width: 600px;
margin: 30px auto;
text-align: center;
font-family: 'Arial', sans-serif; /* ‚≠ê monospace Ï†úÍ±∞ */
}

.detail-buttons {
display: flex;
gap: 20px;
justify-content: center;
}

.buy-btn {
padding: 12px 26px;
background: #ff4d4d;
color: #fff;
border: none;
border-radius: 12px;
font-weight: bold;
cursor: pointer;
}

.cart-btn {
padding: 12px 26px;
background: #333;
color: #fff;
border: none;
border-radius: 12px;
cursor: pointer;
}

