/* Ï†ÑÏ≤¥ Î∞∞Í≤Ω */
.App {
  min-height: 100vh;
  background: url('./img/warm-background.jpg') no-repeat center center fixed;
  background-size: cover;
  font-family: 'Arial', sans-serif;
  color: #333;
}

/* Î©îÏù∏ Ï†úÎ™© */
.App h1 {
  font-size: 5rem;
  margin-top: 100px;
  text-align: center;
  color: #edeff1;
  font-family: 'Arial Rounded MT Bold', 'Poppins', 'Nunito', sans-serif;
}

/* ÏÑúÎ∏åÌÖçÏä§Ìä∏ */
.App p {
  text-align: center;
  font-size: 2.5rem;
  margin-top: 10px;
  color: #70b7c0;
}

.category-bar-fixed {
  position: fixed;
  top: 50px;
  left: 0;
  width: 100%;

  display: flex;
  align-items: center;
  justify-content: center;

  background: none;
  z-index: 1000;
}

.category-center {
  display: flex;
  gap: 50px;
  margin-right: auto;
  margin-left: auto;
}

.category-right {
  position: absolute;
  right: 30px;
}

/* Îπà Í≥µÍ∞Ñ */
.category-spacer {
  height: 1px;
}

.category-button {
  padding: 8px 18px;     /* üîΩ Í∏∞Ï°¥Î≥¥Îã§ ÏûëÍ≤å */
  border-radius: 20px;   /* üîΩ Îçú ÎèôÍ∑∏ÎûóÍ≤å */
  font-size: 14px;
  font-weight: 600;
}

/* Î©îÏù∏ ÏΩòÌÖêÏ∏† Ïó¨Î∞± */
.main-content {
  padding-top: 70px;
}

/* Í≤ÄÏÉâÏ∞Ω */
input[type="text"] {
  width: 400px;
  padding: 15px 20px;
  border-radius: 30px;
  border: 1px solid #ccc;
  font-size: 1rem;
}

/* Í≤ÄÏÉâ Î≤ÑÌäº */
button {
  padding: 15px 30px;
  border-radius: 30px;
  border: none;
  background: #ff4d4d;
  color: #fff;
  cursor: pointer;
  transition: 0.3s;
}

.more-btn {
  padding: 12px 30px;
  border-radius: 30px;
  border: none;
  background: #333;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
}

.more-btn:hover {
  background: #000;
}

button:hover {
  background: #e64444;
}

/* ÏÉÅÌíà Ïπ¥Îìú */
.product-card {
  background: #fff;
  border-radius: 15px;
  padding: 20px;
  width: 240px;
  cursor: pointer;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  text-align: center;
  transition: 0.3s;
}

.product-card:hover {
  transform: translateY(-5px);
}

/* ÏÉÅÌíà Ïù¥ÎØ∏ÏßÄ */
.product-card img {
  width: 100%;
  border-radius: 10px;
  object-fit: cover;
}

/* ÏÉÅÏÑ∏ÌéòÏù¥ÏßÄ */
.product-detail {
  max-width: 1000px;
  margin: 80px auto;
  padding: 40px;
  background: #fff;
  border-radius: 20px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.25);
  text-align: center;
}

.product-detail img {
  width: 100%;
  max-width: 450px;
  border-radius: 15px;
  object-fit: cover;
}

.login-btn {
  padding: 3px 12px;      /* üîΩ ÌÅ¨Í∏∞ Ï§ÑÏù¥Í∏∞ */
  border-radius: 20px;    /* üîΩ Îçú ÎèôÍ∑∏ÎûóÍ≤å */
  font-size: 10px;
}

.category-right .login-btn {
  padding: 6px 14px;
  font-size: 12px;
  border-radius: 18px;
}



/* Îí§Î°úÍ∞ÄÍ∏∞ Î≤ÑÌäº */
.back-button {
  margin-bottom: 0px;
  border: none;
  background: none;
  cursor: pointer;
  font-size: 1rem;
}

/* Î∞òÏùëÌòï */
@media (max-width: 768px) {
  .product-detail {
    padding: 20px;
  }

  .App h1 {
    font-size: 2rem;
  }

  input[type="text"] {
    width: 90%;
  }
}
